import { Controller } from '@ali/4ever-cangjie';
import { getRangesFromEditor } from '@ali/4ever-utils';

function getBlockquoteState(controller: Controller) {
  const ranges = getRangesFromEditor(controller);
  return ranges.some((selection) => {
    const { document } = controller.value;
    const blocks = document.getLeafBlocksAtRange(selection);
    return blocks.some((block) => block?.data?.blockquote);
  });
}

export default getBlockquoteState;
